body {
  background: #197339;
  background-image: url("./fabric-pattern.png");
}

.deck {
  display: flex;
  width: fit-content;
  height: fit-content;
  padding: 2vh;
}

.game {
  display: flex;
  flex-wrap: wrap;
}

.reserved-deck {
  margin: 0vh 2vh;
  position: relative;
}

.card-holder {
  display: flex;
  align-items: center;
  justify-content: space-around;
  border-radius: 1vh;
  height: 21vh;
  width: 16vh;
  border: 1vh solid #040f0fa3;
}

.pile-card {
  position: absolute;
}

.foundation {
  display: flex;
  width: 100%;
  border-bottom: 1vh #040f0fa3 double;
  padding: 0vh 1vh 2vh 0vh;
}

.piles {
  display: flex;
}

.card {
  height: 19vh;
  width: 15vh;
  font-size: 21vh;
  line-height: 0.7;
  user-select: none;
  border-radius: 1vh;
  background: #deded8;
  box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.52);
  border: black 1px solid;
}

.card:hover {
  cursor: grab;
}

.pile {
  min-height: 20vh;
  width: 20vh;
  margin: 1vh;
  position: relative;
}

.tableau {
  display: flex;
  width: 100%;
  margin-top: 3vh;
}

.deck-cards {
  margin-right: 4vh;
}

.reservedDecks {
  margin-left: 25vh;
}
