body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body,
#root {
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #197339;
  background-image: url(/solataire/static/media/fabric-pattern.fe52ed05.png);
}

.deck {
  display: -webkit-flex;
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 2vh;
}

.game {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.reserved-deck {
  margin: 0vh 2vh;
  position: relative;
}

.card-holder {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  border-radius: 1vh;
  height: 21vh;
  width: 16vh;
  border: 1vh solid #040f0fa3;
}

.pile-card {
  position: absolute;
}

.foundation {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  border-bottom: 1vh #040f0fa3 double;
  padding: 0vh 1vh 2vh 0vh;
}

.piles {
  display: -webkit-flex;
  display: flex;
}

.card {
  height: 19vh;
  width: 15vh;
  font-size: 21vh;
  line-height: 0.7;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-radius: 1vh;
  background: #deded8;
  box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.52);
  border: black 1px solid;
}

.card:hover {
  cursor: -webkit-grab;
  cursor: grab;
}

.pile {
  min-height: 20vh;
  width: 20vh;
  margin: 1vh;
  position: relative;
}

.tableau {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  margin-top: 3vh;
}

.deck-cards {
  margin-right: 4vh;
}

.reservedDecks {
  margin-left: 25vh;
}

